}
.app .rslides{position:relative;list-style:none;overflow:hidden;width:100%;padding:0;margin:0
}
.app .rslides li{-webkit-backface-visibility:hidden;position:absolute;display:none;width:100%;left:0;top:0
}
.app .rslides li:first-child{position:relative;display:block;float:left
}
.app .rslides img{display:block;height:auto;float:left;width:100%;border:0
}
.app #loader{position:fixed;width:100%;height:100%;background:#ffffff;z-index:9999;overflow:hidden;left:0
}
.app .cssload-spinner{position:absolute;width:33px;height:33px;left:50%;top:calc(40%)
}
.app .cssload-spinner:after,.app .cssload-spinner:before{position:absolute;content:"";border:2px solid #a1a1a1;width:31px;height:31px
}
.app .cssload-spinner:after{animation:cssload-spinner1 2.88s linear infinite;-o-animation:cssload-spinner1 2.88s linear infinite;-ms-animation:cssload-spinner1 2.88s linear infinite;-webkit-animation:cssload-spinner1 2.88s linear infinite;-moz-animation:cssload-spinner1 2.88s linear infinite
}
.app .cssload-spinner:before{width:43px;height:43px;margin-left:-6.5px;margin-top:-6.5px;animation:cssload-spinner2 2.88s linear infinite;-o-animation:cssload-spinner2 2.88s linear infinite;-ms-animation:cssload-spinner2 2.88s linear infinite;-webkit-animation:cssload-spinner2 2.88s linear infinite;-moz-animation:cssload-spinner2 2.88s linear infinite
}
@keyframes cssload-spinner1{
from{transform:rotate(0deg)
}
to{transform:rotate(360deg)
}
}
@keyframes cssload-spinner2{
from{transform:rotate(0deg)
}
to{transform:rotate(-360deg)
}
}
